import React, { Component } from 'react'
import XLSX from 'xlsx';
import { Table, Button, Modal, Form, Input,message} from 'antd'
import { ExclamationCircleOutlined } from '@ant-design/icons';
//
// import AdvancedSearchForm from '../../components/AdvancedSearchForm'
//
import {getPageRoles,isOpt,ConvertFomrData,downloadDemoFile} from '../../utils'
//API 
import {addBjInfo,delBjInfo,editBjInfo,getBjImp} from '../../api'
// 
import { BjImp_columns } from '../../config/table-columns'
// import { BjInfoFielsds } from '../../config/form-Fields'
//
//import { BjInfo } from '../../anaData'
const { confirm } = Modal;

export default class Service2 extends Component {
    formRef = React.createRef();
    Roles = [];//
    state = { 
        dataSource: [],
        BjImp_columns:[],
        dataTotal: 0,
        loading: true,
        isModalEditShow: false,//
        ModalTitle:'',
        Fuc_Add:false,
        Fuc_Exel:false,
        Fuc_Submit:false,
    }
    //
    SearchData = async (formData) => {
        if(formData===undefined){
            formData = new FormData();
        }else{
            formData = ConvertFomrData(formData);
        }
        this.setState({loading:true})
        const result = await getBjImp(formData);
        if(result.status===0){
            const {BjImp} = result.data;
            this.setState({ dataSource: BjImp,loading:false,BjImp_columns:BjImp_columns})
        }else{
            message.error("");
        }
        
    }
    componentDidMount = async () => {
        let {Fuc_Add,Fuc_Exel,Fuc_Submit} = this.state;
        this.SearchData();
        const {pathname} = this.props.location;
        this.Roles = await getPageRoles(pathname);
        // BjInfo_columns.push(
        //     {
        //         title: '',
        //         width: 50,
        //         fixed: 'right',
        //         dataIndex: 'operation',
        //         render: (_, record) => {
        //             return (
        //                 <div style={{textAlign:'center'}}>
        //                     <Button size="small" type="primary" onClick={()=>this.ModalDel(record)} danger></Button>&emsp;
        //                     <Button size="small" type="primary" onClick={() => this.ModalEdit(record)} ></Button>
        //                 </div>
        //             )
        //         }
        //     }
        // )
        if(isOpt(this.Roles,"")){
            Fuc_Add = true;
        }
        if(isOpt(this.Roles,"Excel")){
            Fuc_Exel = true;
        }
        //
        if(isOpt(this.Roles,"")){
            Fuc_Submit = true;
        }//Fuc_ExcelOut
        this.setState({Fuc_Add,Fuc_Exel,Fuc_Submit})
    }
    componentWillUnmount = ()=>{
        //BjInfo_columns.pop();
    }
    // 
    ModalEdit = (record) => { 
        this.setState({ isModalEditShow: true,ModalTitle:''},()=>{
            this.formRef.current.setFieldsValue(record)
        }) 
    }
    // 
    ModalAdd = ()=>{
        this.setState({ isModalEditShow: true,ModalTitle:''},()=>{
            this.formRef.current.resetFields()
        })
    }
    //
    ModalDel = (record)=>{
        confirm({
            title:'?',
            icon: <ExclamationCircleOutlined />,
            okText: '',
            okType: 'danger',
            cancelText: '',
            onOk() {
                //record.ID
                const formData = new FormData();
                formData.append("ID",record.ID);
                const result = delBjInfo(formData);
                if(result.status===0){
                    message.success("");
                }else{
                    message.error("");
                }

            },
            onCancel() {
            },
        })
    }
    //SAP
    ModalSubmit = ()=>{
        //submitBjInfo
        confirm({
            title:'?',
            icon: <ExclamationCircleOutlined />,
            okText: '',
            okType: 'danger',
            cancelText: '',
            onOk() {
                //record.ID
                //const formData = new FormData();
                //const result = submitBjInfo(formData);
                // if(result.status===0){
                //     message.success("");
                // }else{
                //     message.error("");
                // }

            },
            onCancel() {
            },
        })
    }
    //Excel
    ModalExcel = () => {
        const { history } = this.props;
        //url
        history.push({pathname:"/Admin/Upload/ExcelUp",columns:BjImp_columns,type:1,submitUrl:"BjImp",subTitle:""});
    }
     // / OK
    ModalEditOk = async () => {
        const form = this.formRef.current;
        try {
            await form.validateFields();
        } catch (errorInfo) {
            message.warn('')
            return;
        }
        //
        const json = form.getFieldsValue(true);
        const formData = ConvertFomrData(json); 
        const {ModalTitle} = this.state;
        if(ModalTitle===''){
            // ID
            const result = editBjInfo(formData);
            if(result.status===0){
                message.success("");
            }else{
                message.error("");
            }
        }else if(ModalTitle===''){
            //
            const result = addBjInfo(formData);
            if(result.status===0){
                message.success("");
            }else{
                message.error("");
            }
        }
        this.setState({ isModalEditShow: false })
    }
    ///  
    ModalEditCancel = () => {
        this.setState({ isModalEditShow: false })
    }
    ModalExcelOut = ()=>{
        const {dataSource} = this.state;
        //let obj = [];
        const range = XLSX.utils.decode_range(dataSource);
        console.dir(range);
    }
    DownLoadDemo = ()=>{
        downloadDemoFile(".xls");
    }
    //downloadDemoFile(".xlsx");
    render() {
        const { dataSource, loading, isModalEditShow,ModalTitle,Fuc_Add,Fuc_Exel} = this.state;
        return (
            <div className="main">
                <div className="toolArea">
                    {Fuc_Add?
                    <Button type="primary" onClick={()=>this.ModalAdd()}></Button>:""
                    }
                    &emsp;
                    {Fuc_Exel?
                    <Button type="primary" onClick={() => this.ModalExcel()}>Excel</Button>:""
                    }
                    &emsp;
                    <Button type="primary" onClick={()=>this.DownLoadDemo()} ></Button>
                </div>
                <Table
                dataSource={dataSource}
                bordered
                rowKey="ID"
                sticky={true}
                //scroll={{ x: 2600}} 
                columns={BjImp_columns}
                size="middle"
                loading={loading}
                pagination={false}
                > 
                </Table>
                <Modal title={ModalTitle} visible={isModalEditShow} onOk={() => this.ModalEditOk()} onCancel={() => this.ModalEditCancel()} >
                    <Form ref={this.formRef} >
                        <Form.Item
                            name="FirstCode"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message:''
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name="FirstName"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message:''
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name="SecondCode"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message:''
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name="SecondName"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message:''
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name="ThirdCode"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message:''
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name="ThirdName"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message:''
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name="FourthCode"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message:''
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name="FourthName"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message:''
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name="FifthCode"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message:''
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name="FifthName"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message:''
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name="Prooerty"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message:''
                                },
                            ]}
                        >
                            <Input/>
                        </Form.Item>
                        <Form.Item
                            name="Menge"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message:''
                                },
                            ]}
                        >
                            <Input type="number" />
                        </Form.Item>
                    </Form>
                </Modal>
            </div>
        )
    }
}
