import React, { Component } from 'react'
import { Table, Button, Modal, Form, Input, Select, message } from 'antd'
import { ExclamationCircleOutlined } from '@ant-design/icons';
//
import './index.less'
//
import AdvancedSearchForm from '../../components/AdvancedSearchForm'
//
import { getPageRoles, isOpt, ConvertFomrData,downloadDemoFile} from '../../utils'
//API
import { getWBInfo, editWBInfo, addWBInfo ,delWBInfo,delWBInfos} from '../../api'
//
//import { WBInfo } from '../../anaData'
//
import { WBInfo_columns } from '../../config/table-columns'
import { WBInfoFielsds } from '../../config/form-Fields'

const { Option } = Select;
const { confirm } = Modal;
export default class Matrial extends Component {
    formRef = React.createRef();
    state = {
        dataSource: [],
        dataTotal: 0,
        loading: true,
        isModalEditShow: false,//
        isModalDelShow:false,
        ModalTitle: '',
        WBInfo_columns: [],
        selectedRowKeys:[],
        Fuc_Exel: false,
        Fuc_Add: false,
        Fuc_Edit: false,
        Fuc_Del: false
    }
    //
    SearchData = async (formData) => {
        let { Fuc_Exel, Fuc_Add, Fuc_Edit, Fuc_Del} = this.state;
        const { pathname } = this.props.location;
        this.Roles = await getPageRoles(pathname);
        if (isOpt(this.Roles, "Excel")) {
            Fuc_Exel = true;
        }
        if (isOpt(this.Roles, "")) {
            Fuc_Add = true;
        }
        if (isOpt(this.Roles, "")) {
            Fuc_Edit = true;
        }
        if (isOpt(this.Roles, "")) {
            Fuc_Del = true;
        }
        if(Fuc_Edit||Fuc_Del){
            WBInfo_columns.push({
                title: '',
                width: 50,
                fixed: 'right',
                dataIndex: 'operation',
                render: (_, record) => {
                    return (
                        <div style={{ textAlign: 'center' }}>
                            {Fuc_Del?<Button size="small" type="primary" onClick={() => this.ModalDel(record,this)} danger></Button>:""}
                            &emsp;
                            {Fuc_Edit?<Button size="small" type="primary" onClick={() => this.ModalEdit(record)} ></Button>:""}
                        </div>
                    )
                }
            })
        }
        if (formData === undefined) {
            formData = new FormData();
        } else {
            formData = ConvertFomrData(formData);
        }
        this.setState({ loading: true })
        const result = await getWBInfo(formData);
        
        if (result.status === 0) {
            const { WBInfo } = result.data;
            this.setState({ dataSource: WBInfo, loading: false,WBInfo_columns,Fuc_Add, Fuc_Exel, Fuc_Edit, Fuc_Del})
        } else {
            message.error("");
            this.setState({loading: false,WBInfo_columns,Fuc_Add, Fuc_Exel, Fuc_Edit, Fuc_Del})
        }

    }
    layout = {
        labelCol: {
            span: 4,
        },
        wrapperCol: {
            span: 16,
        },
    };
    //
    ModalEdit = (record) => {
        this.setState({ isModalEditShow: true, ModalTitle: '' }, () => {
            this.formRef.current.setFieldsValue(record)
        })
    }
    // 
    ModalAdd = () => {
        this.setState({ isModalEditShow: true, ModalTitle: '' }, () => {
            this.formRef.current.resetFields()
        })
    }
    //
    ModalDel =  (record,app) => {
        confirm({
            title: '?',
            icon: <ExclamationCircleOutlined />,
            okText: '',
            okType: 'danger',
            cancelText: '',
            async onOk() {
                //record.ID
                const formData = new FormData();
                formData.append("ID",record.ID);
                const result = await delWBInfo(formData);
                if(result.status===0){
                    message.success("");
                    Modal.destroyAll();
                    app.SearchData();
                }else{
                    message.error("");
                    Modal.destroyAll();
                }
            },
            onCancel() {
            },
        })
    }
    ModalDels = (app)=>{
        confirm({
            title: '?',
            icon: <ExclamationCircleOutlined />,
            okText: '',
            okType: 'danger',
            cancelText: '',
            async onOk() {
                const formData = new FormData();
                const IDS =app.state.selectedRowKeys.join(',');
                if(IDS===""){
                    message.warn("");
                    Modal.destroyAll();
                    return;
                }
                formData.append("IDS",IDS);
                const result = await delWBInfos(formData);
                if(result.status===0){
                    message.success("");
                    Modal.destroyAll();
                    app.SearchData();
                }else{
                    message.error("");
                    Modal.destroyAll();
                }
            },
            onCancel() {
                //console.log('Cancel');
            },
        })
        
    }

    //Excel
    ModalExcel = () => {
        const { history } = this.props;
        //url
        history.push({ pathname: "/Admin/Upload/ExcelUp", columns: WBInfo_columns, submitUrl: "WBInfo",subTitle:""});
    }
    // / OK
    ModalEditOk = async () => {
        const form = this.formRef.current;
        try {
            await form.validateFields();
        } catch (errorInfo) {
            message.warn('')
            return;
        }
        //
        //const {WerksID,TypeID,MRP,ID} = form.getFieldsValue(true);
        const { ModalTitle } = this.state;
        const formData = ConvertFomrData(form.getFieldsValue(true))
        if (ModalTitle === '') {
            const result = await editWBInfo(formData);
            if (result.status === 0) {
                message.success("");
                this.SearchData();
            }else{
                message.error("");
            }
        } else if (ModalTitle === '') {
            const result = await addWBInfo(formData);
            if (result.status === 0) {
                message.success("");
                this.SearchData();
            }else{
                message.error("");
            }
        }
        this.setState({ isModalEditShow: false })
    }
    ModalEditCancel = () => {
        this.setState({ isModalEditShow: false })
    }
    getOptions = (options) => {
        return options().map(item => <Option key={item.value + item.text} value={item.value}>{item.text}</Option>)
    }
    onSelectChange = selectedRowKeys =>{
        this.setState({ selectedRowKeys });
    }
    //
    componentDidMount = async () => {
        
        this.SearchData();
        
    }
    DownLoadDemo = ()=>{
        downloadDemoFile(".xlsx");
    }
    componentWillUnmount = () => {
        const {WBInfo_columns,Fuc_Edit,Fuc_Del} = this.state;

        if(Fuc_Edit||Fuc_Del){
            WBInfo_columns.pop();
            this.setState({WBInfo_columns})
        }
    }
    render() {
        const { dataSource, loading, ModalTitle, isModalEditShow,selectedRowKeys ,Fuc_Exel, Fuc_Add,Fuc_Del,WBInfo_columns} = this.state;
        const rowSelection = {
            selectedRowKeys,
            onChange: this.onSelectChange,
          };
        while(WBInfo_columns.length>4){
            WBInfo_columns.pop();
        }
        return (
            <div className="main">
                <div>
                    <AdvancedSearchForm SearchData={this.SearchData} Fielsds={WBInfoFielsds} form={this.form} />
                </div>
                <div className="toolArea">
                    {
                        Fuc_Add ? <Button type="primary" onClick={() => this.ModalAdd()}></Button> : ""
                    }
                    &emsp;
                    {
                        Fuc_Exel ? <Button type="primary" onClick={() => this.ModalExcel()}>Excel</Button> : ""
                    }
                    &emsp;
                    <Button type="primary" onClick={()=>this.DownLoadDemo()} ></Button>
                    &emsp;
                    {Fuc_Del?<Button type="primary" danger onClick={()=>this.ModalDels(this)}></Button>:""}
                </div>
                <Table
                    rowSelection={rowSelection}
                    dataSource={dataSource}
                    bordered
                    rowKey="ID"
                    sticky={true}
                    columns={WBInfo_columns}
                    size="middle"
                    loading={loading}
                    pagination={false}
                >
                </Table>
                <Modal title={ModalTitle} visible={isModalEditShow} onOk={() => this.ModalEditOk()} onCancel={() => this.ModalEditCancel()} >
                    <Form ref={this.formRef}  {...this.layout} >
                        <Form.Item
                            name="Matnr"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message: ''
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name="Maktx"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message: ''
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name="Bz"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message: ''
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                    </Form>
                </Modal>
            </div>
        )

    }
}
