import {Werks} from "../anaData"

//options
export const FactoryList = ()=>{
  //Werks
  const Options = Werks.map(item=>{
    return {value:item.ID,text:item.Name}
  })
  return Options;
}
//
const TypeList = (TypeInfo)=>{
  //Werks
  const Options = TypeInfo.map(item=>{
    return {value:item.ID,text:item.Name}
  })
  return Options;
} 

