//BjImp Excel
export const BjImp_columns =[
    {
        title: '',
        dataIndex: 'Matnr',
        key: 'Matnr',
        width: 60,
    },
    {
        title: '',
        dataIndex: 'Menge',
        key: 'Menge',
        width: 60,
    },
    {
        title:'',
        dataIndex: 'status',
        key: 'status',
        width: 30,
        render:function(val){
            let NewVal;
            switch(val){
                case "":
                    NewVal = <Tag color="success"></Tag>
                    break;
                case "":
                    NewVal = <Tag color="processing"></Tag>
                    break;
                case "":
                    NewVal = <Tag color="error"></Tag>
                    break;
                case "":
                    NewVal = <Tag color="warning"></Tag>
                    break;
                default:
                    NewVal = <Tag color="processing"></Tag>
                    break;
            }
            return NewVal;
        }
    }
]
