import React, { Component } from 'react'
import { Table, Button, Modal, Form, Input,message} from 'antd'
import { ExclamationCircleOutlined } from '@ant-design/icons';
//
// import AdvancedSearchForm from '../../components/AdvancedSearchForm'
//
import {getPageRoles,isOpt,ConvertFomrData,downloadDemoFile} from '../../utils'
//API
import {getJjImp} from '../../api'

// 
import { JiImp_columns} from '../../config/table-columns'
//import {SERVER_ADDRESS_FILEPATH} from '../../utils/StaticSet'
// import { JjInfoFielsds } from '../../config/form-Fields'
// 
//import { JjInfo } from '../../anaData'
const { confirm } = Modal;

export default class Service3 extends Component {
    formRef = React.createRef()
    state = {  
        dataSource: [],
        JiImp_columns:[],
        dataTotal: 0,
        loading: true, 
        isModalEditShow: false,//
        ModalTitle:'' ,
        Fuc_Exel:false
    }
    //
    SearchData = async (formData) => {
        if(formData===undefined){
            formData = new FormData();
        }else{
            formData = ConvertFomrData(formData);
        }
        this.setState({loading:true})
        const result = await getJjImp(formData);
        if(result.status===0){
            const {JjImp} = result.data;
            this.setState({ dataSource: JjImp,loading:false,JiImp_columns:JiImp_columns})
        }else{
            message.error("");
        }
    }
    componentDidMount = async () => {
        let {Fuc_Exel} = this.state;
        this.SearchData();
        const {pathname} = this.props.location;
        this.Roles = await getPageRoles(pathname);
        // JjInfo_columns.push(
        //     {
        //         title: '',
        //         width: 50,
        //         fixed: 'right',
        //         dataIndex: 'operation',
        //         render: (_, record) => {
        //             return (
        //                 <div style={{textAlign:'center'}}>
        //                     <Button size="small" type="primary" onClick={()=>this.ModalDel(record)} danger></Button>&emsp;
        //                     <Button size="small" type="primary" onClick={() => this.ModalEdit(record)} ></Button>
        //                 </div>
        //             )
        //         }
        //     }
        // )
        if(isOpt(this.Roles,"Excel")){
            Fuc_Exel = true;
        }
        this.setState({Fuc_Exel})
    }
    componentWillUnmount = ()=>{
        //JiImp_columns.pop();
    }
    // 
    ModalEdit = (record) => { 
        this.setState({ isModalEditShow: true,ModalTitle:''},()=>{
            this.formRef.current.setFieldsValue(record)
        })
    }
    // 
    ModalAdd = ()=>{
        this.setState({ isModalEditShow: true,ModalTitle:''},()=>{
            this.formRef.current.resetFields()
        })
    }
     //
     ModalDel = (record)=>{
        confirm({
            title:'?',
            icon: <ExclamationCircleOutlined />,
            okText: '',
            okType: 'danger',
            cancelText: '',
            onOk() {
                //record.ID
                console.log('OK');
            },
            onCancel() {
                console.log('Cancel');
            },
        })
    }
    //Excel
    ModalExcel = () => {
        const { history } = this.props;
        //url
        history.push({pathname:"/Admin/Upload/ExcelUp",columns:JiImp_columns,type:1,submitUrl:"JjImp",subTitle:""});
    }
    // / OK
    ModalEditOk = async () => {
        const form = this.formRef.current;
        try {
            await form.validateFields();
        } catch (errorInfo) {
            message.warn('')
            return;
        }
        //
        //const {WerksID,TypeID,MRP,ID} = form.getFieldsValue(true);
        const {ModalTitle} = this.state;
        if(ModalTitle===''){
            // ID
            console.dir(form.getFieldsValue(true));
        }else if(ModalTitle===''){
            //
            console.dir(form.getFieldsValue(true));
        }
        this.setState({ isModalEditShow: false })
    }
    ///  
    ModalEditCancel = () => {
        this.setState({ isModalEditShow: false })
    }
    DownLoadDemo = ()=>{
        downloadDemoFile(".xlsx");
    }
    render() {
        const { dataSource, loading, isModalEditShow,ModalTitle,Fuc_Exel} = this.state;
        return (
            <div className="main">
                <div className="toolArea">
                    {
                        Fuc_Exel?<Button type="primary" onClick={() => this.ModalExcel()}>Excel</Button>:""
                    }
                    &emsp;
                    <Button type="primary" onClick={()=>this.DownLoadDemo()} ></Button>
                </div>
                <Table
                dataSource={dataSource}
                bordered
                rowKey="ID"
                sticky={true}
                //scroll={{ x: 2600}} 
                columns={JiImp_columns}
                size="middle"
                loading={loading}
                pagination={false}
                >
                </Table>
                <Modal title={ModalTitle} visible={isModalEditShow} onOk={() => this.ModalEditOk()} onCancel={() => this.ModalEditCancel()} >
                    <Form ref={this.formRef} >
                        <Form.Item
                            name="Series"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message:''
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name="Matnr"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message:''
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name="Menge"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message:''
                                },
                            ]}
                        >
                            <Input type="number"/>
                        </Form.Item>
                        <Form.Item
                            name="Group"
                            label=""
                            rules={[
                                {
                                    required: true,
                                    message:''
                                },
                            ]}
                        >
                            <Input/>
                        </Form.Item>
                    </Form>
                </Modal>
            </div>
        )
    }
}
