import React, { Component } from 'react'
import {Form,Input,Button,message} from 'antd'
import {Redirect,withRouter} from 'react-router-dom'

import {UserOutlined,LockOutlined} from '@ant-design/icons'
import './login.less'
import logo from '../../assets/images/999.png'
import Layout from 'antd/lib/layout/layout';
import { reqLogin } from '../../api'
import memoryUtils from '../../utils/memoryUtils'
import storageUtils from '../../utils/storageUtils'
const Item = Form.Item;
// const layout={
//     wrapperCol: {
//         offset:0,
//         span: 23 
//     },
// }
// const btnLayout={
//     wrapperCol: {
//         offset: 0,
//         span: 23,
//     },
// }
class Login extends Component {
    formRef = React.createRef();
    handleSubmit = async (event)=>{
        event.preventDefault();
        const form = this.formRef.current;
        const {username,password} = form.getFieldsValue(true);
        var formData = new FormData();
        formData.append("username",username);
        formData.append("password",password);
        const error = form.getFieldsError();
        if(error.find(item=>item.errors.length>0)){
            return;
        }
        //{status:0,data:user}
        const result = await reqLogin(formData);
        if(result.status===0){//
            if(result.data==null || result.data.UserInfo.length===0){
                message.warn("");
                return;
            }
            message.success("");
            //user
            var user;
            for(var key in result.data){
                var temp = result.data[key][0];
                if(typeof(temp) === "object"){
                    user = result.data[key][0];
                    break;
                }
            }
            //const user = result.data.G_UserM[0];
            //user.Roles = user.Roles.replaceAll('#','');
            memoryUtils.user = user;//
            //
            storageUtils.saveUser(user);
            this.props.history.replace('/Page/MainPage');
        }else if(result.status===1){//
            message.error("");
        }else{
            message.error("");
        }
    }
    render() {
        console.log("Login");
        const user = storageUtils.getUser();
        //storageUtils.removeUser();
        //console.log(user.ID);
        if(user && user.ID){
            memoryUtils.user = user;//
            return <Redirect to='/Admin' />
        }
        return (
            <div className="login">
                <div className="login-header">
                    <img src={logo} alt="logo" className="Loginlogo"></img>
                    <h1></h1>
                </div>
                <section className="login-content">
                    <h2></h2>
                    <Form {...Layout} ref={this.formRef} onSubmitCapture={this.handleSubmit}>
                        <Item name="username" rules={[
                            {required:true,message:''},
                            {pattern:/^[a-zA-Z0-9_]+$/,message:'' },
                            {whitespace:true,message:''}
                        ]} >
                            <Input placeholder="" prefix={<UserOutlined style={{color:'rgba(0,0,0,0.25)'}}></UserOutlined>} />
                        </Item>
                        <Item name="password" rules={[
                            {required:true,message:''},
                            {pattern:/^[a-zA-Z0-9_@]+$/,message:'@' },
                            {whitespace:true,message:''}
                        ]} >
                            <Input.Password placeholder="" prefix={<LockOutlined style={{color:'rgba(0,0,0,0.25)'}} />} />
                        </Item>
                        <Item>
                            <Button className="login-button" type="primary" htmlType="submit"></Button>
                        </Item>
                    </Form>
                </section>
            </div>
        )
    }
}

export default withRouter(Login);